body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.addTodo-section {
  padding-top: 60px;
}
.book {
  color: #2692ef;
}
.addTodo-heading h2 {
  font-family: cursive;
  font-size: 30px;
  padding-bottom: 25px;
  color: black;
  font-weight: 900;
}
label {
  display: inline-block;
  margin-bottom: .5rem;
  font-family: cursive;
  font-size: 15px;
  font-weight: 600;
}
.btn-primary {
  color: #fff;
  background-color: #2692ef!important;
  border-color: #2692ef!important;
  font-weight: 500;
  font-family: cursive;
}
.noTodo-info h6 {
  font-size: 12px;
  color: red;
  font-weight: 700;
  font-family: cursive;
}
.btn-danger {
  color: #fff;
  background-color: #dc3545;
  border-color: #dc3545;
  padding: 0px 6px 1px 6px;
}
.table-bordered thead td, .table-bordered thead th {
  border-bottom-width: 2px;
  font-family: cursive;
  font-size: 14px!important;
}
.table td, .table th {
  padding: .75rem;
  vertical-align: top;
  border-top: 1px solid #dee2e6;
  font-family: cursive;
}
.footer-section {
  background-color: #000;
  padding: 17px;
}
.footer-content h6 {
  color: #fff;
}
.navbar-light .navbar-brand {
  color: rgba(0,0,0,.9);
  font-family: cursive;
  font-weight: 700;
}